___CSS_LOADER_EXPORT___.push([module.id, ".analog-clock[data-v-3db173fe]{border:2px solid #000;border-radius:50%;height:80px;margin:auto;position:relative;width:80px}.hand[data-v-3db173fe]{background:#000;bottom:50%;left:50%;position:absolute;transform-origin:bottom}.hour[data-v-3db173fe]{height:30px;width:4px}.minute[data-v-3db173fe]{height:40px;width:2px}.second[data-v-3db173fe]{background:red;height:45px;transition:transform .05s linear;width:1px}", "",{"version":3,"sources":["webpack://./resources/js/Components/Global/Time/TimezoneClocks.vue"],"names":[],"mappings":"AAyEA,+BAIE,qBAAuB,CACvB,iBAAkB,CAFlB,WAAY,CAGZ,WAAY,CALZ,iBAAkB,CAClB,UAKF,CAEA,uBAKE,eAAiB,CAHjB,UAAW,CACX,QAAS,CAFT,iBAAkB,CAGlB,uBAEF,CAEA,uBAEE,WAAY,CADZ,SAEF,CAEA,yBAEE,WAAY,CADZ,SAEF,CAEA,yBAGE,cAAe,CADf,WAAY,CAEZ,gCAAkC,CAHlC,SAIF","sourcesContent":["<template>\n  <div class=\"flex flex-wrap justify-center items-center space-x-8\">\n    <div v-for=\"timezone in timezones\" :key=\"timezone.name\" class=\"text-center\">\n      <div class=\"analog-clock\" :class=\"timezone.name\">\n        <div class=\"hand hour\" :style=\"hourStyle(timezone.offset)\"></div>\n        <div class=\"hand minute\" :style=\"minuteStyle\"></div>\n        <div class=\"hand second\" :style=\"secondStyle\"></div>\n      </div>\n      <p class=\"mt-2\">{{ timezone.name }}</p>\n      <p>{{ getFormattedTime(timezone.offset) }}</p>\n    </div>\n  </div>\n</template>\n\n<script setup>\nimport { ref, computed, onMounted, onUnmounted } from 'vue';\nimport { useUserStore } from \"@/Stores/UserStore\";\nimport dayjs from 'dayjs';\nimport utc from 'dayjs/plugin/utc';\nimport timezone from 'dayjs/plugin/timezone';\n\ndayjs.extend(utc);\ndayjs.extend(timezone);\n\nconst userStore = useUserStore();\n\nconst timezones = [\n  { name: 'Pacific Time', offset: 'America/Los_Angeles' },\n  { name: 'Central Time', offset: 'America/Chicago' },\n  { name: 'Eastern Time', offset: 'America/New_York' },\n  { name: 'London Time', offset: 'Europe/London' },\n  { name: 'Sydney Time', offset: 'Australia/Sydney' },\n  { name: 'Tokyo Time', offset: 'Asia/Tokyo' }\n];\n\nconst now = ref(dayjs());\n\nconst getFormattedTime = (offset) => dayjs().tz(offset).format('HH:mm:ss');\n\nconst updateTime = () => {\n  now.value = dayjs();\n};\n\nconst hourStyle = (offset) => {\n  const hours = now.value.tz(offset).hour();\n  const degrees = ((hours % 12) / 12) * 360;\n  return { transform: `rotate(${degrees}deg)` };\n};\n\nconst minuteStyle = computed(() => {\n  const minutes = now.value.minute();\n  const degrees = (minutes / 60) * 360;\n  return { transform: `rotate(${degrees}deg)` };\n});\n\nconst secondStyle = computed(() => {\n  const seconds = now.value.second();\n  const degrees = (seconds / 60) * 360;\n  return { transform: `rotate(${degrees}deg)`, transition: 'transform 0.05s linear' };\n});\n\nlet interval;\n\nonMounted(() => {\n  interval = setInterval(updateTime, 1000);\n});\n\nonUnmounted(() => {\n  clearInterval(interval);\n});\n</script>\n\n<style scoped>\n.analog-clock {\n  position: relative;\n  width: 80px;\n  height: 80px;\n  border: 2px solid black;\n  border-radius: 50%;\n  margin: auto;\n}\n\n.hand {\n  position: absolute;\n  bottom: 50%;\n  left: 50%;\n  transform-origin: bottom;\n  background: black;\n}\n\n.hour {\n  width: 4px;\n  height: 30px;\n}\n\n.minute {\n  width: 2px;\n  height: 40px;\n}\n\n.second {\n  width: 1px;\n  height: 45px;\n  background: red;\n  transition: transform 0.05s linear;\n}\n</style>\n"],"sourceRoot":""}]);
